@import url(https://api.mapbox.com/mapbox-gl-js/v1.8.0/mapbox-gl.css);
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: linear-gradient(to top, rgba(7, 27, 82, 1) 0%, rgba(0, 128, 128, 1) 100%);
}
.mapContainer {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
}

.button {
    cursor: pointer;
    display: inline-block;
    position: absolute;
    left: 40px;
    top: 21px
}


